import { injectGlobal } from 'styled-components'
import CircularBook from './statics/fonts/CircularStd-Book.woff2'
import CircularMedium from './statics/fonts/CircularStd-Medium.woff2'
import CircularBold from './statics/fonts/CircularStd-Bold.woff2'
import Kelson from './statics/fonts/KelsonSans-Bold.woff2'
import styled from 'styled-components';

injectGlobal`
  @font-face {
    font-family: 'CircularStd';
    src: url(${CircularBook}) format("woff2");
    font-weight:400;
    font-style:normal;
  }

  @font-face {
    font-family: 'CircularStd';
    src: url(${CircularMedium}) format("woff2");
    font-weight:500;
    font-style:normal;
  }

  @font-face {
    font-family: 'CircularStd';
    src: url(${CircularBold}) format("woff2");
    font-weight:600;
    font-style:normal;
  }

  @font-face {
    font-family: 'Kelson';
    src:url(${Kelson}) format("woff2");
  }

  a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {
    font-family: CircularStd,sans-serif;
    font-weight:100;
    font-size:17px;
    line-height:1.5;
}

ol, ul {
    list-style: none;
}

::-webkit-scrollbar {display:none}
`